// EditOsobaModal.js
import React, { useState, useEffect } from "react";
import { Modal, Button, Form } from "react-bootstrap";

const EditOsobaModal = ({ show, osoba, onHide, onSave, jwtUser }) => {
  const [editedOsoba, setEditedOsoba] = useState({ ...osoba });
  const getJwtUser = () => {
    const token = localStorage.getItem("token");
    if (!token) return null;

    try {
      const payload = JSON.parse(atob(token.split(".")[1]));
      return payload.sub; // ili payload.userId, zavisi od toga ta koristi
    } catch {
      return null;
    }
  };
  const userId = getJwtUser();
  console.log(userId, "userId iz JWT");

  useEffect(() => {
    setEditedOsoba({ ...osoba }); // resetuje formu pri otvaranju
  }, [osoba]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedOsoba((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    onSave(editedOsoba, jwtUser); // poziva onSave iz roditelja
    onHide();
  };

  return (
    <Modal show={show} onHide={onHide}>
      <Modal.Header closeButton>
        <Modal.Title>Izmeni osobu</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group>
            <Form.Label>Ime</Form.Label>
            <Form.Control
              name="ime"
              value={editedOsoba.ime}
              onChange={handleChange}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Prezime</Form.Label>
            <Form.Control
              name="prezime"
              value={editedOsoba.prezime}
              onChange={handleChange}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Grad</Form.Label>
            <Form.Control
              name="grad"
              value={editedOsoba.grad}
              onChange={handleChange}
            />
          </Form.Group>
          {/* Dodaj jo polja ako treba */}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>
          Zatvori
        </Button>
        <Button variant="primary" onClick={handleSubmit}>
          Sauvaj izmene
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default EditOsobaModal;
