import React, { useEffect, useState } from "react";
import axios from "axios";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";
import { Modal, Form } from "react-bootstrap";
import Button from "react-bootstrap/Button";

const OsobaDetalji = ({ osobaId }) => {
  const [osoba, setOsoba] = useState(null);
  const [liked, setLiked] = useState(false);

  const [showLikes, setShowLikes] = useState(false);

  const toggleLikes = () => {
    setShowLikes((prev) => !prev);
  };
  const getJwtUser = () => {
    const token = localStorage.getItem("token");
    if (!token) return null;

    try {
      const payload = JSON.parse(atob(token.split(".")[1]));
      return payload.sub; // ili payload.userId, zavisi od toga ta koristi
    } catch {
      return null;
    }
  };
  const userId = getJwtUser();

  useEffect(() => {
    const token = localStorage.getItem("token");

    const socket = new SockJS(`http://localhost:8080/ws?token=${token}`, null, {
      transports: ["websocket"], // koristi samo websocket
    });

    const stompClient = new Client({
      webSocketFactory: () => socket,
      connectHeaders: {
        Authorization: `Bearer ${token}`, // opciono
      },
      debug: (str) => {
        console.log("[WS Debug]", str);
      },
      onConnect: () => {
        console.log("Connected to WebSocket osoba detalji");

        stompClient.subscribe(`/topic/osobe/${osobaId}`, (message) => {
          const updatedOsoba = JSON.parse(message.body);
          setOsoba(updatedOsoba);
          console.log("==updatedOsoba==", updatedOsoba);
        });
      },
      onStompError: (frame) => {
        console.error("STOMP error", frame);
      },
    });

    stompClient.activate();

    return () => {
      stompClient.deactivate();
    };
  }, [osobaId]);

  const getOsoba = () => {
    axios
      .get(`http://localhost:8080/api/osobe/${osobaId}`)
      .then((res) => {
        setOsoba(res.data);
        setLiked(isLikedByLoggedInUser(res.data, userId));
        // console.log(
        //   res.data,
        //   "==osoba==",
        //   isLikedByLoggedInUser(res.data, userId)
        // );
      })
      .catch((err) => console.error("Greka pri uitavanju osobe:", err));
  };
  useEffect(() => {
    getOsoba();
  }, [osobaId]);

  if (!osoba) return <p>Uitavanje...</p>;
  const handleChange = (e) => {
    const { name, value } = e.target;
    setOsoba((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  function isLikedByLoggedInUser(item, userId) {
    if (
      item?.likes.some((like) => like.user.username === userId && like.liked)
    ) {
      return true;
    } else {
      return false;
    }
  }

  const handleToggleLike = () => {
    const token = localStorage.getItem("token");

    axios
      .post(
        `http://localhost:8080/api/osobe/${osobaId}/like`,
        { liked: !liked },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setOsoba(res.data);
        setLiked(!liked);
      })
      .catch((err) => console.error(err));
  };

  return (
    <div style={{ padding: 20, border: "1px solid gray", borderRadius: 8 }}>
      <img
        src={`data:image/jpeg;base64,${osoba.slika}`}
        alt={`${osoba.ime} ${osoba.prezime}`}
        style={{
          width: "40px",
          height: "40px",
          borderRadius: "50%",
        }}
      />
      {/* <img
        src={`/uploads/${osoba.slikaUrl}`}
        alt="Osoba"
        style={{ width: 150 }}
      /> */}
      <h2>
        {osoba.ime} {osoba.prezime}
      </h2>
      <p>
        <strong>Grad:</strong> {osoba.grad}
      </p>
      <p>
        <strong>Lepota:</strong> {osoba.lepota}/5
      </p>
      <p>
        <strong>Pamet:</strong> {osoba.pamet}/5
      </p>
      <p>
        <strong>Visina:</strong> {osoba.visina}/5
      </p>
      <button className="btn btn-primary" onClick={handleToggleLike}>
        {liked ? "Unlajkuj" : "Lajkuj"}
        {/* <i className="fa-solid fa-thumbs-up" style={{ color: "blue" }}></i> */}
      </button>
      <p>
        <strong>Lajkova:</strong> {osoba.brojLajkova}{" "}
        <i className="fa-solid fa-thumbs-up" style={{ color: "blue" }}></i>
      </p>

      <div className="border rounded p-3 mb-3 bg-light">
        <button onClick={toggleLikes} className="btn btn-primary mb-2">
          {showLikes ? "Sakrij lajkae" : "Prikai lajkae"}
        </button>

        {showLikes && (
          <ul className="list-group">
            {osoba.likes.map((like, index) => (
              <li key={index} className="list-group-item">
                {like.user.username}
              </li>
            ))}
          </ul>
        )}
      </div>

      <h3>Komentari:</h3>
      <ul>
        {osoba.komentari.map((k, index) => (
          <li key={index}>{k.tekst}</li>
        ))}
      </ul>
    </div>
  );
};

export default OsobaDetalji;
