import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import "./Osobe.css"; // Import your CSS file for styling
import EditOsobaModal from "./Modal/EditOsobaModal";
import gradovi from "../../data/gradovi"; // Import your gradovi data
import Select from "react-select";
import { Container } from "react-bootstrap";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";

let stompClient = null;

const Osobe = () => {
  const [izabraniGrad, setIzabraniGrad] = useState(null);
  const [osobe, setOsobe] = useState([]);
  const [newOsoba, setNewOsoba] = useState({
    ime: "",
    prezime: "",
    grad: "",
    // slikaUrl: "",
    slika: null, // vie nije slikaUrl, ve fajl
    lepota: null,
    pamet: null,
    visina: null,
    brojLajkova: 0,
  });
  const [showForm, setShowForm] = useState(false); // Kontrola prikaza forme
  const opcije = gradovi.map((grad) => ({
    value: grad,
    label: grad,
  }));
  useEffect(() => {
    if (izabraniGrad) {
      setNewOsoba((prev) => ({
        ...prev,
        grad: izabraniGrad.value,
      }));
    }
  }, [izabraniGrad]);

  const getAllOsobe = () => {
    axios
      .get("http://localhost:8080/api/osobe/all")
      .then((response) => {
        setOsobe(response.data);
        console.log(response, "response.dataeee==");
      })
      .catch((error) => console.error("Error fetching data: ", error));
  };
  useEffect(() => {
    getAllOsobe();
    connectWebSocket();
  }, []);
  const connectWebSocket = () => {
    const token = localStorage.getItem("token");

    // Dodaj token u URL kao query parametar i koristi samo websocket transport
    const socket = new SockJS(`http://localhost:8080/ws?token=${token}`, null, {
      transports: ["websocket"], // <-- samo websocket, bez fallbacka
    });

    stompClient = new Client({
      webSocketFactory: () => socket,
      connectHeaders: {
        Authorization: `Bearer ${token}`, // dodatna autentifikacija u headeru, moe ostaviti
      },
      onConnect: () => {
        console.log("Connected to WebSocket");

        stompClient.subscribe("/topic/likes", (message) => {
          const updated = JSON.parse(message.body);
          console.log(
            "=message.body============",
            message.body.likes,
            "=message.body============"
          );
          setOsobe((prev) =>
            prev.map((osoba) =>
              osoba.id === updated.id
                ? { ...osoba, brojLajkova: updated.brojLajkova }
                : osoba
            )
          );
        });
      },
      onStompError: (frame) => {
        console.error("STOMP error", frame);
      },
    });

    stompClient.activate();
  };

  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedOsoba, setSelectedOsoba] = useState(null);
  const jwtUser = "korisnik_iz_tokena"; // napravi funkciju koja to ita iz JWT
  const handleEdit = (id) => {
    const osoba = osobe.find((o) => o.id === id);
    setSelectedOsoba(osoba);
    setShowEditModal(true);
  };
  // const handleDetalji = (id) => {
  //   const osoba = osobe.find((o) => o.id === id);
  //   setSelectedOsoba(osoba);
  //   setShowEditModal(true);
  // };

  const handleUpdate = (updatedOsoba, jwtUser) => {
    axios
      .put(`http://localhost:8080/api/osobe/${updatedOsoba.id}`, updatedOsoba, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("jwt")}`,
        },
      })
      .then((res) => {
        setOsobe((prev) =>
          prev.map((o) => (o.id === updatedOsoba.id ? updatedOsoba : o))
        );
        console.log("Izmenjena osoba:", res.data, "izmenio korisnik:", jwtUser);
      })
      .catch((err) => console.error("Greka prilikom izmene:", err));
  };

  const handleDelete = (id) => {
    console.log("Delete osoba sa id:", id);
    axios
      .delete(`http://localhost:8080/api/osobe/${id}`)
      .then((response) => {
        // Po uspenom brisanju auriraj stanje
        setOsobe(osobe.filter((osoba) => osoba.id !== id));
        console.log("Osoba obrisana:", response);
      })
      .catch((err) => console.error("Error deleting data:", err));
  };

  const handleAdd = () => {
    const formData = new FormData();
    formData.append("ime", newOsoba.ime);
    formData.append("prezime", newOsoba.prezime);
    formData.append("grad", newOsoba.grad);
    formData.append("lepota", newOsoba.lepota);
    formData.append("pamet", newOsoba.pamet);
    formData.append("visina", newOsoba.visina);
    formData.append("brojLajkova", newOsoba.brojLajkova);
    if (newOsoba.slika) {
      formData.append("slika", newOsoba.slika); // Samo ako postoji slika
    }
    // formData.append("slika", newOsoba.slika); // Ovde dodajemo fajl

    axios
      .post("http://localhost:8080/api/osobe/createOsoba", formData, {
        headers: {
          "Content-Type": "multipart/form-data", // Dodajemo header za form-data
        },
      })
      .then((response) => {
        setOsobe([...osobe, response.data]);
        setNewOsoba({
          ime: "",
          prezime: "",
          grad: "",
          slika: null,
          lepota: null,
          pamet: null,
          visina: null,
          brojLajkova: 0,
        });
        setIzabraniGrad(null); // <- OVO dodaj da poniti selektovani grad
        setShowForm(false);
        console.log("Nova osoba dodata:", response);
      })
      .catch((error) => console.error("Error adding osoba:", error));
  };

  // Funkcija za promenu vrednosti u formi
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewOsoba((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  return (
    <div className="container">
      <div className="row">
        <button
          className="btn btn-success mb-3"
          onClick={() => setShowForm(!showForm)} // Toggles form visibility
        >
          Dodaj Osobu
        </button>

        {showForm && (
          <div className="mb-3">
            <h3>Dodaj Novu Osobu</h3>
            <form>
              <input
                type="text"
                name="ime"
                placeholder="Ime"
                value={newOsoba.ime}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="text"
                name="prezime"
                placeholder="Prezime"
                value={newOsoba.prezime}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <Container className="form-control mb-2">
                {/* <h6>Gradovi</h6> */}
                <Select
                  options={opcije}
                  value={izabraniGrad}
                  onChange={(selectedOption) => {
                    setIzabraniGrad(selectedOption);
                    setNewOsoba((prev) => ({
                      ...prev,
                      grad: selectedOption ? selectedOption.value : "",
                    }));
                  }}
                  placeholder="Izaberi grad..."
                  isClearable
                />
                {/* {izabraniGrad && (
                  <p className="mt-3">
                    Izabrani grad: <strong>{izabraniGrad.label}</strong>
                  </p>
                )} */}
              </Container>
              {/* <input
                type="text"
                name="grad"
                placeholder="Grad"
                value={newOsoba.grad}
                onChange={handleInputChange}
                className="form-control mb-2"
              /> */}

              <input
                type="file"
                name="slika"
                onChange={(e) => {
                  setNewOsoba((prev) => ({
                    ...prev,
                    slika: e.target.files[0],
                  }));
                }}
                className="form-control mb-2"
              />
              <label className="mt-2">Lepota (1-5)</label>
              <input
                type="number"
                name="lepota"
                placeholder="Lepota (1-5)"
                min="1"
                max="5"
                value={newOsoba.lepota}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              {/* <label className="mt-2">Lepota (1-5)</label> */}
              <input
                type="number"
                name="pamet"
                placeholder="Pamet (1-5)"
                min="1"
                max="5"
                value={newOsoba.pamet}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="number"
                name="visina"
                placeholder="Visina (1-5)"
                value={newOsoba.visina}
                min="1"
                max="5"
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="number"
                name="brojLajkova"
                placeholder="Broj Lajkova"
                value={newOsoba.brojLajkova}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <button
                type="button"
                className="btn btn-primary"
                onClick={handleAdd}
              >
                Dodaj Osobu
              </button>
            </form>
          </div>
        )}

        <div
          className="table-responsive"
          style={{ width: "100%", maxWidth: "1600px", margin: "0 auto" }}
        >
          <table className="table table-secondary table-striped">
            <thead>
              <tr className="headingTable">
                <th scope="col">Rb</th>
                <th scope="col">Slika</th>
                <th scope="col">Ime</th>
                <th scope="col">Prezime</th>
                <th scope="col">Grad</th>
                <th scope="col">Lepota </th>
                <th scope="col">Pamet </th>
                <th scope="col">Visina </th>
                <th scope="col">Broj Lajkova</th>
                <th scope="col">Details</th>
                <th scope="col"> Edit</th>
                <th scope="col"> Delete</th>
              </tr>
            </thead>
            <tbody>
              {Array.isArray(osobe) &&
                osobe?.map((data, index) => (
                  <tr key={data.id}>
                    <td>{index + 1}</td> {/* Redni broj */}
                    <td>
                      <img
                        src={`data:image/jpeg;base64,${data.slika}`}
                        alt={`${data.ime} ${data.prezime}`}
                        style={{
                          width: "40px",
                          height: "40px",
                          borderRadius: "50%",
                        }}
                      />
                    </td>
                    <td>{data.ime}</td>
                    <td>{data.prezime}</td>
                    <td>{data.grad}</td>
                    <td>{data.lepota}</td>
                    <td>{data.pamet}</td>
                    <td>{data.visina}</td>
                    <td>{data.brojLajkova}</td>
                    {/* <td>
                    <button
                      className="btn btn-primary btn-sm"
                      onClick={() => handleDetalji(data.id)}
                    >
                      Detalji
                    </button>
                  </td> */}
                    <td>
                      <Link to={`/osobe/${data.id}`}>
                        <button className="btn btn-primary btn-sm">
                          Detalji
                        </button>
                      </Link>
                    </td>
                    <td>
                      <button
                        className="btn btn-primary btn-sm"
                        onClick={() => handleEdit(data.id)}
                      >
                        Edit
                      </button>
                    </td>
                    <td>
                      <button
                        className="btn btn-danger btn-sm ml-2"
                        onClick={() => handleDelete(data.id)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
          {/* modal */}
          <EditOsobaModal
            show={showEditModal}
            osoba={selectedOsoba}
            onHide={() => setShowEditModal(false)}
            onSave={handleUpdate}
            jwtUser={jwtUser}
          />

          {/* kraj modala */}
        </div>
      </div>
    </div>
  );
};

export default Osobe;
